@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary: #6948ff;
  --secondary: #553bcb;
  --darkgrey: #22262a;
  --lightgrey: #d3d3d3;
  --grey: #808080;
  --white: #f8f8f8;
  --lightblue: #8bc9ff;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #f8f8f8;
  color: var(--white);
}

body {
  background-color: #22262a;
  background-color: var(--darkgrey);
  color: #d3d3d3;
  color: var(--lightgrey);
  font-family: 'Poppins', sans-serif;
  line-height: 1.5;
  max-width: 2500px;
  margin: 0 auto;
}

.primary-color {
  color: #6948ff;
  color: var(--primary);
}
.navbar {
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px;
    margin-top: 10px;
    background-color: var(--darkgrey);
}

.title {
    color: var(--lightblue);
    padding-top: 10px;
    font-size: 30px;
}

.navbar img {
    width: 80px;
}

.nav-menu {
    display: flex;
}

.nav-item {
    padding: 0 20px;
    font-size: 20px;
    transition: 0.4s;
}

.nav-item:hover {
    opacity: 0.6
}

.hamburger {
    display: none;
    cursor: pointer;
}

@media screen and (max-width: 700px) {
    .nav-menu {
        position: fixed;
        left: -100%;
        top: 100px;
        flex-direction: column;
        justify-content: center;
        background-color: var(--darkgrey);
        width:100%;
        height: 100%;
        text-align: center;
        transition: 0.4s;
        z-index: 999;
        padding-bottom: 80px;
    }

    .nav-menu.active {
        left: 0;
    }

    .nav-item {
        margin: 1.5rem;
        font-size: 1.8rem;
        justify-content: center;
    }

    .hamburger {
        display: flex;
    }

    .title {
        color: #8bc9ff;
        margin-top: 22px;
        margin-left: -30px;
        font-size: 24px;
    }
}
.project-card {
    background: rgb(80, 80, 80);
    border-radius: 10px;
    margin: 10px; 
    padding: 5px;
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    box-shadow: 0 8px 16px rgba(0,0,0,0.07);
}

