.cards {
    background: var(--darkgrey);
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    color: rgb(191, 53, 53);
    width: 80vw;
    margin: 50px auto 50px;
    align-items: center;
    text-align: center;
    justify-content: center;
    grid-gap: 50px;
    gap: 50px;
    max-width: 1250px;
}


@media screen and (max-width:750px) {

    .cards {
        grid-template-columns: 1fr;
        width: 100vw;
    }
}





.about-container {       
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-content: center;
    align-items: center;
    margin: 60px auto;
    grid-template-rows: 1fr 1fr;
    max-width: 1500px;
    height: 88vh;
    grid-gap: 40px;
    gap: 40px;
    margin-left: 20px;
    margin-right: 20px;
}

.desc-container { 
    margin: 0 auto;
    align-self: center;
}

.img-container {
    margin: 0 auto;
