@media screen and (max-width: 750px) {
    .about-container {       
        display: grid;
        grid-template-columns: 1fr;
        justify-content: center;
        align-items: center;
        margin: 0;
        grid-template-rows: 1fr;
        margin-top: 30px;
        width: 100%;
    }

    .logo-container {
        grid-column: span 1;
        grid-template-columns: repeat(4, 1fr);
        margin-top: 60px;
        padding-bottom: 30px;
        margin-bottom: 50px;
    }

    h1 {
        margin: 40px auto;
    }

    p {
        font-size: 18px;
        margin-top: 40px;
        width: 80vw;
        text-align: left;
        margin-right: 0;
    }

